@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

.App {
  height: 100vh;
  background-size: cover;
  color: #fff;
  text-align: center;
}
.App > * {
  display: flex;
  align-items: center;
  justify-content: center;
}


.GeoLocation { height: 10%; justify-content: space-evenly; }

.WeatherForcast {
  height: 80%;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  background-color: rgba(0, 0, 0, 0.4);
  flex-direction: column;
}

.WeatherIcon {
  width: 200px;
}
.Weather {
  font-size: 1.8em;
  font-weight: 700;
}
.Location {
  font-size: 1.5em;
}
.AvgTemprature {
  font-size: 2em;
  font-weight: 700;
}
.HumPre, .Wind {
  font-size: 0.8em;
  padding: 5px;
}

.Footer { height: 10%; }

input, select {
  width: 60vw;
  border: none;
  outline: none;
  padding: 10px;
  font-size: 1em;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
}

select {
  width: 20vw;
}

